'use client';

import { TelegramUser, TelegramWebAppData } from '@/types/telegram';

export class TelegramService {
  private static instance: TelegramService;
  private isInitialized = false;

  static getInstance(): TelegramService {
    if (!TelegramService.instance) {
      TelegramService.instance = new TelegramService();
    }
    return TelegramService.instance;
  }

  initialize(): void {
    if (typeof window !== 'undefined' && window.Telegram?.WebApp) {
      window.Telegram.WebApp.ready();
      window.Telegram.WebApp.expand();
      this.isInitialized = true;
      
      // Apply theme colors
      this.applyTheme();
    }
  }

  private applyTheme(): void {
    const webApp = this.getWebApp();
    if (!webApp) return;

    const theme = webApp.themeParams;
    const root = document.documentElement;

    if (theme.bg_color) {
      root.style.setProperty('--tg-bg-color', theme.bg_color);
    }
    if (theme.text_color) {
      root.style.setProperty('--tg-text-color', theme.text_color);
    }
    if (theme.hint_color) {
      root.style.setProperty('--tg-hint-color', theme.hint_color);
    }
    if (theme.link_color) {
      root.style.setProperty('--tg-link-color', theme.link_color);
    }
    if (theme.button_color) {
      root.style.setProperty('--tg-button-color', theme.button_color);
    }
    if (theme.button_text_color) {
      root.style.setProperty('--tg-button-text-color', theme.button_text_color);
    }
  }

  getWebApp() {
    if (typeof window !== 'undefined' && window.Telegram?.WebApp) {
      return window.Telegram.WebApp;
    }
    return null;
  }

  getUser(): TelegramUser | null {
    const webApp = this.getWebApp();
    return webApp?.initDataUnsafe?.user || null;
  }

  getInitData(): string {
    const webApp = this.getWebApp();
    return webApp?.initData || '';
  }

  getInitDataUnsafe(): TelegramWebAppData | null {
    const webApp = this.getWebApp();
    return webApp?.initDataUnsafe || null;
  }

  showMainButton(text: string, callback: () => void): void {
    const webApp = this.getWebApp();
    if (webApp?.MainButton) {
      webApp.MainButton.setText(text);
      webApp.MainButton.show();
      webApp.MainButton.onClick(callback);
    }
  }

  hideMainButton(): void {
    const webApp = this.getWebApp();
    if (webApp?.MainButton) {
      webApp.MainButton.hide();
    }
  }

  showBackButton(callback: () => void): void {
    const webApp = this.getWebApp();
    if (webApp?.BackButton) {
      webApp.BackButton.show();
      webApp.BackButton.onClick(callback);
    }
  }

  hideBackButton(): void {
    const webApp = this.getWebApp();
    if (webApp?.BackButton) {
      webApp.BackButton.hide();
    }
  }

  showAlert(message: string, callback?: () => void): void {
    const webApp = this.getWebApp();
    if (webApp?.showAlert) {
      webApp.showAlert(message, callback);
    }
  }

  showConfirm(message: string, callback: (confirmed: boolean) => void): void {
    const webApp = this.getWebApp();
    if (webApp?.showConfirm) {
      webApp.showConfirm(message, callback);
    }
  }

  sendData(data: string): void {
    const webApp = this.getWebApp();
    if (webApp?.sendData) {
      webApp.sendData(data);
    }
  }

  triggerHapticFeedback(type: 'light' | 'medium' | 'heavy' | 'rigid' | 'soft'): void {
    const webApp = this.getWebApp();
    if (webApp?.HapticFeedback?.impactOccurred) {
      webApp.HapticFeedback.impactOccurred(type);
    }
  }

  triggerNotificationFeedback(type: 'error' | 'success' | 'warning'): void {
    const webApp = this.getWebApp();
    if (webApp?.HapticFeedback?.notificationOccurred) {
      webApp.HapticFeedback.notificationOccurred(type);
    }
  }

  openLink(url: string): void {
    const webApp = this.getWebApp();
    if (webApp?.openLink) {
      webApp.openLink(url);
    }
  }

  openTelegramLink(url: string): void {
    const webApp = this.getWebApp();
    if (webApp?.openTelegramLink) {
      webApp.openTelegramLink(url);
    }
  }

  close(): void {
    const webApp = this.getWebApp();
    if (webApp?.close) {
      webApp.close();
    }
  }

  isReady(): boolean {
    return this.isInitialized && this.getWebApp() !== null;
  }

  getColorScheme(): 'light' | 'dark' {
    const webApp = this.getWebApp();
    return webApp?.colorScheme || 'light';
  }

  getVersion(): string {
    const webApp = this.getWebApp();
    return webApp?.version || '6.0';
  }

  getPlatform(): string {
    const webApp = this.getWebApp();
    return webApp?.platform || 'unknown';
  }
}

export default TelegramService;
