'use client'

import { useEffect, useState } from 'react'
import TelegramService from '@/lib/telegram'

interface WithdrawalMethod {
  id: string
  name: string
  description: string
  minAmount: number
  maxAmount: number
  processingTime: string
  fee: number
  icon: string
  available: boolean
}

interface WithdrawalRequest {
  id: string
  amount: number
  method: string
  address: string
  status: 'pending' | 'processing' | 'completed' | 'failed'
  created_at: string
  completed_at?: string
  fee: number
}

export default function Withdraw() {
  const [balance, setBalance] = useState<number>(0)
  const [amount, setAmount] = useState<string>('')
  const [selectedMethod, setSelectedMethod] = useState<string>('')
  const [address, setAddress] = useState<string>('')
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [success, setSuccess] = useState<string | null>(null)
  const [withdrawalHistory, setWithdrawalHistory] = useState<WithdrawalRequest[]>([])

  const withdrawalMethods: WithdrawalMethod[] = [
    {
      id: 'telegram_stars',
      name: 'Telegram Stars',
      description: 'Instant withdrawal to Telegram Stars',
      minAmount: 100,
      maxAmount: 10000,
      processingTime: 'Instant',
      fee: 0,
      icon: '‚≠ê',
      available: true
    },
    {
      id: 'ton_wallet',
      name: 'TON Wallet',
      description: 'Withdraw to your TON cryptocurrency wallet',
      minAmount: 500,
      maxAmount: 50000,
      processingTime: '1-24 hours',
      fee: 10,
      icon: 'üíé',
      available: true
    },
    {
      id: 'usdt_trc20',
      name: 'USDT (TRC20)',
      description: 'Withdraw as USDT on TRON network',
      minAmount: 1000,
      maxAmount: 100000,
      processingTime: '1-24 hours',
      fee: 25,
      icon: 'üí∞',
      available: false // Disabled for demo
    }
  ]

  useEffect(() => {
    loadWithdrawData()
  }, [])

  const loadWithdrawData = async () => {
    try {
      const user = TelegramService.getInstance().getUser()
      
      if (!user) {
        setError('User not authenticated')
        return
      }

      // Mock data - in production, this would fetch from API
      setBalance(1250) // Mock balance

      const mockHistory: WithdrawalRequest[] = [
        {
          id: '1',
          amount: 500,
          method: 'telegram_stars',
          address: 'Telegram Stars',
          status: 'completed',
          created_at: new Date(Date.now() - 86400000).toISOString(),
          completed_at: new Date(Date.now() - 86000000).toISOString(),
          fee: 0
        },
        {
          id: '2',
          amount: 1000,
          method: 'ton_wallet',
          address: 'UQD...xyz123',
          status: 'pending',
          created_at: new Date(Date.now() - 3600000).toISOString(),
          fee: 10
        }
      ]

      setWithdrawalHistory(mockHistory)

    } catch (err) {
      console.error('Failed to load withdrawal data:', err)
    }
  }

  const handleWithdraw = async (e: React.FormEvent) => {
    e.preventDefault()
    
    try {
      setLoading(true)
      setError(null)
      setSuccess(null)

      const numericAmount = parseInt(amount)
      
      if (!numericAmount || numericAmount <= 0) {
        setError('Please enter a valid amount')
        return
      }

      if (!selectedMethod) {
        setError('Please select a withdrawal method')
        return
      }

      const method = withdrawalMethods.find(m => m.id === selectedMethod)
      if (!method) {
        setError('Invalid withdrawal method')
        return
      }

      if (!method.available) {
        setError('This withdrawal method is currently unavailable')
        return
      }

      if (numericAmount < method.minAmount) {
        setError(`Minimum withdrawal amount is ${method.minAmount} coins`)
        return
      }

      if (numericAmount > method.maxAmount) {
        setError(`Maximum withdrawal amount is ${method.maxAmount} coins`)
        return
      }

      const totalAmount = numericAmount + method.fee
      if (totalAmount > balance) {
        setError(`Insufficient balance. You need ${totalAmount} coins (including ${method.fee} fee)`)
        return
      }

      if (!address.trim()) {
        setError('Please enter a withdrawal address')
        return
      }

      const user = TelegramService.getInstance().getUser()
      
      if (!user) {
        setError('User not authenticated')
        return
      }

      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 2000))

      // Create mock withdrawal request
      const newWithdrawal: WithdrawalRequest = {
        id: Date.now().toString(),
        amount: numericAmount,
        method: selectedMethod,
        address: address.trim(),
        status: 'pending',
        created_at: new Date().toISOString(),
        fee: method.fee
      }

      setWithdrawalHistory(prev => [newWithdrawal, ...prev])
      setBalance(prev => prev - totalAmount)

      setSuccess(`Withdrawal request submitted successfully! You will receive ${numericAmount} coins after processing.`)
      
      // Reset form
      setAmount('')
      setAddress('')
      setSelectedMethod('')

      // Trigger success haptic feedback
      const telegramService = TelegramService.getInstance()
      telegramService.triggerNotificationFeedback('success')

    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to submit withdrawal request')
      
      // Trigger error haptic feedback
      const telegramService = TelegramService.getInstance()
      telegramService.triggerNotificationFeedback('error')
    } finally {
      setLoading(false)
    }
  }

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'completed': return 'text-green-600 bg-green-100'
      case 'processing': return 'text-blue-600 bg-blue-100'
      case 'pending': return 'text-yellow-600 bg-yellow-100'
      case 'failed': return 'text-red-600 bg-red-100'
      default: return 'text-gray-600 bg-gray-100'
    }
  }

  const formatDate = (dateString: string) => {
    const date = new Date(dateString)
    return date.toLocaleDateString('en-US', { 
      month: 'short', 
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    })
  }

  const selectedMethodData = withdrawalMethods.find(m => m.id === selectedMethod)

  return (
    <div className="space-y-6">
      {/* Balance Card */}
      <div className="bg-gradient-to-r from-green-500 to-emerald-600 rounded-2xl shadow-lg p-6 text-white">
        <h2 className="text-lg font-medium opacity-90 mb-2">Available Balance</h2>
        <div className="text-4xl font-bold mb-1">
          {balance.toLocaleString()}
        </div>
        <p className="text-green-100">coins ready for withdrawal</p>
      </div>

      {/* Withdrawal Form */}
      <div className="bg-white rounded-2xl shadow-sm p-6">
        <h2 className="text-xl font-semibold mb-6 text-gray-800">Withdraw Coins</h2>
        
        <form onSubmit={handleWithdraw} className="space-y-6">
          {/* Withdrawal Method Selection */}
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-3">
              Select Withdrawal Method
            </label>
            <div className="space-y-3">
              {withdrawalMethods.map(method => (
                <div
                  key={method.id}
                  className={`relative border-2 rounded-xl p-4 cursor-pointer transition-all ${
                    selectedMethod === method.id
                      ? 'border-blue-500 bg-blue-50'
                      : method.available
                      ? 'border-gray-200 hover:border-gray-300'
                      : 'border-gray-100 bg-gray-50 cursor-not-allowed opacity-60'
                  }`}
                  onClick={() => method.available && setSelectedMethod(method.id)}
                >
                  <div className="flex items-center justify-between">
                    <div className="flex items-center">
                      <span className="text-2xl mr-3">{method.icon}</span>
                      <div>
                        <h3 className="font-semibold text-gray-900">{method.name}</h3>
                        <p className="text-sm text-gray-600">{method.description}</p>
                        <div className="flex items-center text-xs text-gray-500 mt-1">
                          <span>Min: {method.minAmount}</span>
                          <span className="mx-2">‚Ä¢</span>
                          <span>Max: {method.maxAmount}</span>
                          <span className="mx-2">‚Ä¢</span>
                          <span>Fee: {method.fee} coins</span>
                        </div>
                      </div>
                    </div>
                    <div className="text-right">
                      <div className="text-sm font-medium text-gray-700">{method.processingTime}</div>
                      {!method.available && (
                        <div className="text-xs text-red-500 mt-1">Coming Soon</div>
                      )}
                    </div>
                  </div>
                  
                  {selectedMethod === method.id && (
                    <div className="absolute top-2 right-2">
                      <div className="w-6 h-6 bg-blue-500 rounded-full flex items-center justify-center">
                        <span className="text-white text-sm">‚úì</span>
                      </div>
                    </div>
                  )}
                </div>
              ))}
            </div>
          </div>

          {/* Amount Input */}
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Withdrawal Amount
            </label>
            <div className="relative">
              <input
                type="number"
                value={amount}
                onChange={(e) => setAmount(e.target.value)}
                placeholder="Enter amount"
                className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                min={selectedMethodData?.minAmount || 1}
                max={selectedMethodData?.maxAmount || balance}
              />
              <div className="absolute right-3 top-3 text-gray-500">coins</div>
            </div>
            {selectedMethodData && (
              <div className="mt-2 text-sm text-gray-600">
                Available: {balance} coins ‚Ä¢ Min: {selectedMethodData.minAmount} ‚Ä¢ Max: {selectedMethodData.maxAmount}
                {selectedMethodData.fee > 0 && (
                  <span className="text-orange-600"> ‚Ä¢ Fee: {selectedMethodData.fee} coins</span>
                )}
              </div>
            )}
          </div>

          {/* Address Input */}
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              {selectedMethod === 'telegram_stars' ? 'Telegram Username' : 'Wallet Address'}
            </label>
            <input
              type="text"
              value={address}
              onChange={(e) => setAddress(e.target.value)}
              placeholder={
                selectedMethod === 'telegram_stars' 
                  ? '@username or user ID' 
                  : selectedMethod === 'ton_wallet'
                  ? 'UQD...'
                  : 'Enter wallet address'
              }
              className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            />
          </div>

          {/* Summary */}
          {selectedMethodData && amount && parseInt(amount) > 0 && (
            <div className="bg-gray-50 rounded-xl p-4">
              <h4 className="font-medium text-gray-800 mb-2">Withdrawal Summary</h4>
              <div className="space-y-1 text-sm">
                <div className="flex justify-between">
                  <span className="text-gray-600">Amount:</span>
                  <span className="font-medium">{parseInt(amount)} coins</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-gray-600">Fee:</span>
                  <span className="font-medium">{selectedMethodData.fee} coins</span>
                </div>
                <div className="flex justify-between border-t pt-1 mt-2">
                  <span className="text-gray-800 font-medium">Total Deducted:</span>
                  <span className="font-bold">{parseInt(amount) + selectedMethodData.fee} coins</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-gray-600">You'll receive:</span>
                  <span className="font-medium text-green-600">{parseInt(amount)} coins</span>
                </div>
              </div>
            </div>
          )}

          {/* Error/Success Messages */}
          {error && (
            <div className="p-4 bg-red-50 border border-red-200 rounded-xl">
              <p className="text-red-600 text-sm">{error}</p>
            </div>
          )}

          {success && (
            <div className="p-4 bg-green-50 border border-green-200 rounded-xl">
              <p className="text-green-600 text-sm">{success}</p>
            </div>
          )}

          {/* Submit Button */}
          <button
            type="submit"
            disabled={loading || !selectedMethod || !amount || !address}
            className="w-full bg-blue-600 text-white px-6 py-4 rounded-xl font-semibold hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed transition-all duration-200 transform hover:scale-105 active:scale-95"
          >
            {loading ? (
              <div className="flex items-center justify-center">
                <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-white mr-2"></div>
                Processing...
              </div>
            ) : (
              'Submit Withdrawal Request'
            )}
          </button>
        </form>
      </div>

      {/* Withdrawal History */}
      {withdrawalHistory.length > 0 && (
        <div className="bg-white rounded-2xl shadow-sm p-6">
          <h3 className="text-lg font-semibold mb-4 text-gray-800">
            Withdrawal History ({withdrawalHistory.length})
          </h3>
          
          <div className="space-y-3">
            {withdrawalHistory.map(withdrawal => (
              <div key={withdrawal.id} className="flex justify-between items-center p-4 bg-gray-50 rounded-xl">
                <div className="flex items-center">
                  <div className="w-10 h-10 bg-blue-100 rounded-full flex items-center justify-center mr-3">
                    <span className="text-blue-600">
                      {withdrawalMethods.find(m => m.id === withdrawal.method)?.icon || 'üí∞'}
                    </span>
                  </div>
                  <div>
                    <p className="font-medium text-gray-900">
                      {withdrawal.amount.toLocaleString()} coins
                    </p>
                    <div className="flex items-center text-xs text-gray-500">
                      <span>{formatDate(withdrawal.created_at)}</span>
                      <span className="mx-2">‚Ä¢</span>
                      <span>{withdrawalMethods.find(m => m.id === withdrawal.method)?.name}</span>
                    </div>
                    <p className="text-xs text-gray-500 truncate max-w-32">
                      {withdrawal.address}
                    </p>
                  </div>
                </div>
                <div className="text-right">
                  <span className={`px-2 py-1 rounded-full text-xs font-medium ${getStatusColor(withdrawal.status)}`}>
                    {withdrawal.status}
                  </span>
                  {withdrawal.fee > 0 && (
                    <p className="text-xs text-gray-500 mt-1">Fee: {withdrawal.fee}</p>
                  )}
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* Important Notes */}
      <div className="bg-yellow-50 border border-yellow-200 rounded-2xl p-6">
        <h4 className="font-semibold text-yellow-800 mb-3">‚ö†Ô∏è Important Notes</h4>
        <ul className="text-sm text-yellow-700 space-y-2">
          <li>‚Ä¢ Withdrawals are processed manually and may take up to 24 hours</li>
          <li>‚Ä¢ Double-check your wallet address before submitting</li>
          <li>‚Ä¢ Minimum withdrawal amounts apply to cover network fees</li>
          <li>‚Ä¢ Failed withdrawals will be refunded to your balance</li>
          <li>‚Ä¢ Contact support if you don't receive your withdrawal within 48 hours</li>
        </ul>
      </div>
    </div>
  )
}
