'use client'

import { useEffect, useState } from 'react'
import TelegramService from '@/lib/telegram'

interface Transaction {
  id: string
  type: 'earn' | 'spend' | 'withdraw'
  amount: number
  source: string
  description: string
  created_at: string
  status?: 'completed' | 'pending' | 'failed'
}

interface WalletData {
  balance: number
  totalEarned: number
  totalWithdrawn: number
  transactions: Transaction[]
}

export default function Wallet() {
  const [walletData, setWalletData] = useState<WalletData | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [refreshing, setRefreshing] = useState(false)

  useEffect(() => {
    loadWalletData()
  }, [])

  const loadWalletData = async (isRefresh = false) => {
    try {
      if (isRefresh) {
        setRefreshing(true)
      } else {
        setLoading(true)
      }
      setError(null)
      
      const user = TelegramService.getInstance().getUser()
      
      if (!user) {
        setError('User not authenticated')
        return
      }

      // For now, we'll use mock data. In production, this would fetch from /api/wallet
      const mockTransactions: Transaction[] = [
        {
          id: '1',
          type: 'earn',
          amount: 50,
          source: 'daily_bonus',
          description: 'Daily check-in bonus',
          created_at: new Date().toISOString(),
          status: 'completed'
        },
        {
          id: '2',
          type: 'earn',
          amount: 10,
          source: 'ad_view',
          description: 'Watched advertisement',
          created_at: new Date(Date.now() - 3600000).toISOString(),
          status: 'completed'
        },
        {
          id: '3',
          type: 'earn',
          amount: 100,
          source: 'referral',
          description: 'Friend joined via your referral',
          created_at: new Date(Date.now() - 7200000).toISOString(),
          status: 'completed'
        },
        {
          id: '4',
          type: 'earn',
          amount: 200,
          source: 'task_completion',
          description: 'Completed social media task',
          created_at: new Date(Date.now() - 86400000).toISOString(),
          status: 'completed'
        },
        {
          id: '5',
          type: 'withdraw',
          amount: 100,
          source: 'telegram_stars',
          description: 'Withdrawal to Telegram Stars',
          created_at: new Date(Date.now() - 172800000).toISOString(),
          status: 'pending'
        }
      ]

      // Simulate API delay
      await new Promise(resolve => setTimeout(resolve, isRefresh ? 500 : 1000))

      const totalEarned = mockTransactions
        .filter(t => t.type === 'earn')
        .reduce((sum, t) => sum + t.amount, 0)

      const totalWithdrawn = mockTransactions
        .filter(t => t.type === 'withdraw' && t.status === 'completed')
        .reduce((sum, t) => sum + t.amount, 0)

      const balance = totalEarned - totalWithdrawn

      setWalletData({
        balance,
        totalEarned,
        totalWithdrawn,
        transactions: mockTransactions.sort((a, b) => 
          new Date(b.created_at).getTime() - new Date(a.created_at).getTime()
        )
      })

    } catch (err) {
      console.error('Failed to load wallet data:', err)
      setError(err instanceof Error ? err.message : 'Failed to load wallet data')
    } finally {
      setLoading(false)
      setRefreshing(false)
    }
  }

  const handleRefresh = () => {
    const telegramService = TelegramService.getInstance()
    telegramService.triggerHapticFeedback('light')
    loadWalletData(true)
  }

  const formatDate = (dateString: string) => {
    const date = new Date(dateString)
    const now = new Date()
    const diffInHours = (now.getTime() - date.getTime()) / (1000 * 60 * 60)

    if (diffInHours < 1) {
      return 'Just now'
    } else if (diffInHours < 24) {
      return `${Math.floor(diffInHours)}h ago`
    } else if (diffInHours < 48) {
      return 'Yesterday'
    } else {
      return date.toLocaleDateString()
    }
  }

  const getTransactionIcon = (transaction: Transaction) => {
    if (transaction.type === 'earn') {
      switch (transaction.source) {
        case 'ad_view': return 'üì∫'
        case 'referral': return 'üë•'
        case 'daily_bonus': return 'üìÖ'
        case 'task_completion': return '‚úÖ'
        default: return 'üí∞'
      }
    } else if (transaction.type === 'withdraw') {
      return 'üí∏'
    }
    return 'üí∞'
  }

  const getStatusColor = (status?: string) => {
    switch (status) {
      case 'completed': return 'text-green-600'
      case 'pending': return 'text-yellow-600'
      case 'failed': return 'text-red-600'
      default: return 'text-gray-600'
    }
  }

  if (loading) {
    return (
      <div className="space-y-6">
        {/* Balance Card Skeleton */}
        <div className="bg-white rounded-2xl shadow-sm p-6 animate-pulse">
          <div className="h-6 bg-gray-200 rounded w-32 mb-4"></div>
          <div className="h-12 bg-gray-200 rounded w-48 mb-2"></div>
          <div className="h-4 bg-gray-200 rounded w-24"></div>
        </div>

        {/* Stats Skeleton */}
        <div className="grid grid-cols-2 gap-4">
          <div className="bg-white rounded-2xl shadow-sm p-4 animate-pulse">
            <div className="h-8 bg-gray-200 rounded w-16 mb-2"></div>
            <div className="h-4 bg-gray-200 rounded w-20"></div>
          </div>
          <div className="bg-white rounded-2xl shadow-sm p-4 animate-pulse">
            <div className="h-8 bg-gray-200 rounded w-16 mb-2"></div>
            <div className="h-4 bg-gray-200 rounded w-20"></div>
          </div>
        </div>

        {/* Transactions Skeleton */}
        <div className="bg-white rounded-2xl shadow-sm p-6 animate-pulse">
          <div className="h-6 bg-gray-200 rounded w-40 mb-4"></div>
          {[1, 2, 3].map((i) => (
            <div key={i} className="flex justify-between items-center py-3 border-b border-gray-100 last:border-b-0">
              <div className="flex items-center">
                <div className="w-10 h-10 bg-gray-200 rounded-full mr-3"></div>
                <div>
                  <div className="h-4 bg-gray-200 rounded w-32 mb-1"></div>
                  <div className="h-3 bg-gray-200 rounded w-20"></div>
                </div>
              </div>
              <div className="h-4 bg-gray-200 rounded w-16"></div>
            </div>
          ))}
        </div>
      </div>
    )
  }

  if (error) {
    return (
      <div className="bg-red-50 border border-red-200 rounded-2xl p-6">
        <div className="text-center">
          <div className="text-4xl mb-2">üí≥</div>
          <h3 className="text-lg font-semibold text-red-800 mb-2">Error Loading Wallet</h3>
          <p className="text-red-600 mb-4">{error}</p>
          <button
            onClick={() => loadWalletData()}
            className="bg-red-600 text-white px-6 py-2 rounded-lg font-medium hover:bg-red-700 transition-colors"
          >
            Try Again
          </button>
        </div>
      </div>
    )
  }

  if (!walletData) {
    return (
      <div className="bg-gray-50 rounded-2xl p-8 text-center">
        <div className="text-4xl mb-3">üí∞</div>
        <h3 className="text-lg font-semibold text-gray-700 mb-2">No Wallet Data</h3>
        <p className="text-gray-600">Unable to load wallet information</p>
      </div>
    )
  }

  return (
    <div className="space-y-6">
      {/* Balance Card */}
      <div className="bg-gradient-to-r from-blue-600 to-purple-600 rounded-2xl shadow-lg p-6 text-white">
        <div className="flex justify-between items-start mb-4">
          <div>
            <h2 className="text-lg font-medium opacity-90">Your Balance</h2>
            <div className="text-4xl font-bold mb-1">
              {walletData.balance.toLocaleString()}
            </div>
            <p className="text-blue-100">coins</p>
          </div>
          <button
            onClick={handleRefresh}
            disabled={refreshing}
            className="p-2 bg-white/20 rounded-full hover:bg-white/30 transition-colors"
          >
            <div className={`w-5 h-5 ${refreshing ? 'animate-spin' : ''}`}>
              üîÑ
            </div>
          </button>
        </div>
        
        <div className="flex justify-between text-sm opacity-90">
          <span>Available for withdrawal</span>
          <span>üíé Premium Balance</span>
        </div>
      </div>

      {/* Stats Grid */}
      <div className="grid grid-cols-2 gap-4">
        <div className="bg-white rounded-2xl shadow-sm p-4">
          <div className="text-2xl font-bold text-green-600 mb-1">
            {walletData.totalEarned.toLocaleString()}
          </div>
          <p className="text-sm text-gray-600">Total Earned</p>
        </div>
        
        <div className="bg-white rounded-2xl shadow-sm p-4">
          <div className="text-2xl font-bold text-blue-600 mb-1">
            {walletData.totalWithdrawn.toLocaleString()}
          </div>
          <p className="text-sm text-gray-600">Total Withdrawn</p>
        </div>
      </div>

      {/* Recent Transactions */}
      <div className="bg-white rounded-2xl shadow-sm p-6">
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-lg font-semibold text-gray-800">Recent Transactions</h2>
          <span className="text-sm text-gray-500">
            {walletData.transactions.length} transactions
          </span>
        </div>
        
        {walletData.transactions.length === 0 ? (
          <div className="text-center py-8">
            <div className="text-4xl mb-3">üìä</div>
            <p className="text-gray-600">No transactions yet</p>
            <p className="text-sm text-gray-500 mt-1">Start earning by completing tasks!</p>
          </div>
        ) : (
          <div className="space-y-3">
            {walletData.transactions.slice(0, 10).map(transaction => (
              <div key={transaction.id} className="flex justify-between items-center p-3 bg-gray-50 rounded-xl">
                <div className="flex items-center">
                  <div className="w-10 h-10 bg-white rounded-full flex items-center justify-center mr-3 shadow-sm">
                    <span className="text-lg">{getTransactionIcon(transaction)}</span>
                  </div>
                  <div>
                    <p className="font-medium text-gray-900">{transaction.description}</p>
                    <div className="flex items-center text-xs text-gray-500">
                      <span>{formatDate(transaction.created_at)}</span>
                      {transaction.status && (
                        <>
                          <span className="mx-1">‚Ä¢</span>
                          <span className={getStatusColor(transaction.status)}>
                            {transaction.status}
                          </span>
                        </>
                      )}
                    </div>
                  </div>
                </div>
                <div className={`font-semibold ${
                  transaction.type === 'earn' ? 'text-green-600' : 
                  transaction.type === 'withdraw' ? 'text-red-600' : 'text-gray-600'
                }`}>
                  {transaction.type === 'earn' ? '+' : '-'}
                  {transaction.amount.toLocaleString()}
                </div>
              </div>
            ))}
            
            {walletData.transactions.length > 10 && (
              <div className="text-center pt-4">
                <button className="text-blue-600 text-sm font-medium hover:text-blue-700">
                  View All Transactions
                </button>
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  )
}
