'use client'

import { useEffect, useState } from 'react'
import TelegramService from '@/lib/telegram'

interface Task {
  id: string
  title: string
  description: string
  reward: number
  type: 'social' | 'ad_view' | 'referral' | 'daily_bonus'
  status: 'available' | 'completed' | 'expired'
  dailyLimit: number
  completedCount: number
  emoji: string
}

export default function TaskList() {
  const [tasks, setTasks] = useState<Task[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [completingTask, setCompletingTask] = useState<string | null>(null)

  useEffect(() => {
    loadTasks()
  }, [])

  const loadTasks = async () => {
    try {
      setLoading(true)
      setError(null)
      
      const user = TelegramService.getInstance().getUser()
      
      if (!user) {
        setError('User not authenticated')
        return
      }

      // For now, we'll use mock data. In production, this would fetch from /api/tasks
      const mockTasks: Task[] = [
        {
          id: '1',
          title: 'Daily Check-in',
          description: 'Check in daily to earn bonus coins',
          reward: 50,
          type: 'daily_bonus',
          status: 'available',
          dailyLimit: 1,
          completedCount: 0,
          emoji: 'üìÖ'
        },
        {
          id: '2',
          title: 'Follow on Telegram',
          description: 'Follow our official Telegram channel',
          reward: 100,
          type: 'social',
          status: 'available',
          dailyLimit: 1,
          completedCount: 0,
          emoji: 'üì¢'
        },
        {
          id: '3',
          title: 'Share with Friends',
          description: 'Share TapWave with 3 friends',
          reward: 200,
          type: 'referral',
          status: 'available',
          dailyLimit: 3,
          completedCount: 0,
          emoji: 'üë•'
        },
        {
          id: '4',
          title: 'Watch 5 Ads',
          description: 'Watch 5 advertisements today',
          reward: 25,
          type: 'ad_view',
          status: 'available',
          dailyLimit: 5,
          completedCount: 0,
          emoji: 'üì∫'
        }
      ]

      // Simulate API delay
      await new Promise(resolve => setTimeout(resolve, 1000))
      
      setTasks(mockTasks)
    } catch (err) {
      console.error('Failed to load tasks:', err)
      setError(err instanceof Error ? err.message : 'Failed to load tasks')
    } finally {
      setLoading(false)
    }
  }

  const completeTask = async (taskId: string) => {
    try {
      setCompletingTask(taskId)
      setError(null)
      
      const user = TelegramService.getInstance().getUser()
      
      if (!user) {
        setError('User not authenticated')
        return
      }

      const task = tasks.find(t => t.id === taskId)
      if (!task) {
        setError('Task not found')
        return
      }

      // Handle different task types
      if (task.type === 'social') {
        // Open Telegram channel
        const telegramService = TelegramService.getInstance()
        telegramService.openTelegramLink('https://t.me/tapwave_official')
      } else if (task.type === 'referral') {
        // This would typically open the referral sharing interface
        const telegramService = TelegramService.getInstance()
        telegramService.triggerHapticFeedback('medium')
      }

      // Simulate API call to complete task
      await new Promise(resolve => setTimeout(resolve, 1500))

      // Update task status locally
      setTasks(prevTasks => 
        prevTasks.map(t => 
          t.id === taskId 
            ? { 
                ...t, 
                status: t.completedCount + 1 >= t.dailyLimit ? 'completed' : 'available',
                completedCount: t.completedCount + 1 
              }
            : t
        )
      )

      // Trigger success feedback
      const telegramService = TelegramService.getInstance()
      telegramService.triggerNotificationFeedback('success')
      telegramService.showAlert(`Task completed! You earned ${task.reward} coins.`)

    } catch (err) {
      console.error('Failed to complete task:', err)
      setError(err instanceof Error ? err.message : 'Failed to complete task')
      
      // Trigger error feedback
      const telegramService = TelegramService.getInstance()
      telegramService.triggerNotificationFeedback('error')
    } finally {
      setCompletingTask(null)
    }
  }

  if (loading) {
    return (
      <div className="space-y-4">
        {[1, 2, 3].map((i) => (
          <div key={i} className="bg-white rounded-2xl p-6 animate-pulse">
            <div className="flex justify-between items-start mb-3">
              <div className="h-6 bg-gray-200 rounded w-3/4"></div>
              <div className="h-6 bg-gray-200 rounded w-16"></div>
            </div>
            <div className="h-4 bg-gray-200 rounded w-full mb-4"></div>
            <div className="flex justify-between items-center">
              <div className="h-4 bg-gray-200 rounded w-24"></div>
              <div className="h-10 bg-gray-200 rounded w-20"></div>
            </div>
          </div>
        ))}
      </div>
    )
  }

  if (error) {
    return (
      <div className="bg-red-50 border border-red-200 rounded-2xl p-6">
        <div className="text-center">
          <div className="text-4xl mb-2">‚ùå</div>
          <h3 className="text-lg font-semibold text-red-800 mb-2">Error Loading Tasks</h3>
          <p className="text-red-600 mb-4">{error}</p>
          <button
            onClick={loadTasks}
            className="bg-red-600 text-white px-6 py-2 rounded-lg font-medium hover:bg-red-700 transition-colors"
          >
            Try Again
          </button>
        </div>
      </div>
    )
  }

  const availableTasks = tasks.filter(task => task.status === 'available' && task.completedCount < task.dailyLimit)
  const completedTasks = tasks.filter(task => task.status === 'completed' || task.completedCount >= task.dailyLimit)

  return (
    <div className="space-y-6">
      {/* Available Tasks */}
      <div>
        <h2 className="text-xl font-semibold mb-4 text-gray-800">
          Available Tasks ({availableTasks.length})
        </h2>
        
        {availableTasks.length === 0 ? (
          <div className="bg-gray-50 rounded-2xl p-8 text-center">
            <div className="text-4xl mb-3">üéØ</div>
            <h3 className="text-lg font-semibold text-gray-700 mb-2">No Tasks Available</h3>
            <p className="text-gray-600">Check back later for new tasks and opportunities to earn!</p>
          </div>
        ) : (
          <div className="space-y-4">
            {availableTasks.map(task => (
              <div key={task.id} className="bg-white rounded-2xl shadow-sm p-6 border border-gray-100">
                <div className="flex justify-between items-start mb-3">
                  <div className="flex items-center">
                    <span className="text-2xl mr-3">{task.emoji}</span>
                    <div>
                      <h3 className="font-semibold text-gray-900">{task.title}</h3>
                      <span className="inline-block px-2 py-1 bg-blue-100 text-blue-800 text-xs rounded-full mt-1">
                        {task.type.replace('_', ' ').toUpperCase()}
                      </span>
                    </div>
                  </div>
                  <div className="text-right">
                    <span className="text-lg font-bold text-green-600">+{task.reward}</span>
                    <div className="text-xs text-gray-500">coins</div>
                  </div>
                </div>
                
                <p className="text-gray-600 mb-4">{task.description}</p>
                
                <div className="flex justify-between items-center">
                  <div className="text-sm text-gray-500">
                    Progress: {task.completedCount}/{task.dailyLimit} today
                  </div>
                  
                  <button
                    onClick={() => completeTask(task.id)}
                    disabled={completingTask === task.id || task.completedCount >= task.dailyLimit}
                    className="bg-blue-600 text-white px-6 py-2 rounded-xl font-medium hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed transition-all duration-200 transform hover:scale-105 active:scale-95"
                  >
                    {completingTask === task.id ? (
                      <div className="flex items-center">
                        <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
                        Completing...
                      </div>
                    ) : task.completedCount >= task.dailyLimit ? (
                      'Limit Reached'
                    ) : (
                      'Complete Task'
                    )}
                  </button>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>

      {/* Completed Tasks */}
      {completedTasks.length > 0 && (
        <div>
          <h2 className="text-xl font-semibold mb-4 text-gray-800">
            Completed Tasks ({completedTasks.length})
          </h2>
          <div className="space-y-3">
            {completedTasks.map(task => (
              <div key={task.id} className="bg-green-50 border border-green-200 rounded-xl p-4">
                <div className="flex justify-between items-center">
                  <div className="flex items-center">
                    <span className="text-xl mr-3">{task.emoji}</span>
                    <div>
                      <span className="font-medium text-green-800">{task.title}</span>
                      <div className="text-sm text-green-600">
                        Completed {task.completedCount}/{task.dailyLimit}
                      </div>
                    </div>
                  </div>
                  <div className="flex items-center text-green-600">
                    <span className="text-sm font-medium">+{task.reward * task.completedCount} coins</span>
                    <span className="ml-2">‚úÖ</span>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  )
}
