'use client'

import { useEffect, useState } from 'react'
import TelegramService from '@/lib/telegram'
import { TelegramUser } from '@/types/telegram'
import AdComponent from '@/components/AdComponent'
import TaskList from '@/components/TaskList'
import Wallet from '@/components/Wallet'
import Referrals from '@/components/Referrals'
import Withdraw from '@/components/Withdraw'

type TabType = 'dashboard' | 'tasks' | 'wallet' | 'referrals' | 'withdraw'

export default function Home() {
  const [user, setUser] = useState<TelegramUser | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [activeTab, setActiveTab] = useState<TabType>('dashboard')
  const [isAuthenticated, setIsAuthenticated] = useState(false)

  useEffect(() => {
    initializeApp()
  }, [])

  const initializeApp = async () => {
    try {
      setLoading(true)
      setError(null)

      // Initialize Telegram WebApp
      const telegramService = TelegramService.getInstance()
      telegramService.initialize()

      // Check if running in Telegram
      if (!telegramService.isReady()) {
        setError('This app must be opened through Telegram')
        setLoading(false)
        return
      }

      // Get user from Telegram
      const telegramUser = telegramService.getUser()
      if (!telegramUser) {
        setError('Unable to get user information from Telegram')
        setLoading(false)
        return
      }

      setUser(telegramUser)

      // Authenticate with backend
      const initData = telegramService.getInitData()
      if (!initData) {
        setError('No initialization data available')
        setLoading(false)
        return
      }

      const response = await fetch('/api/auth/telegram', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ initData }),
      })

      if (!response.ok) {
        const errorData = await response.json()
        throw new Error(errorData.error || 'Authentication failed')
      }

      const authData = await response.json()
      setIsAuthenticated(true)
      
      // Trigger success haptic feedback
      telegramService.triggerNotificationFeedback('success')

    } catch (err) {
      console.error('App initialization error:', err)
      setError(err instanceof Error ? err.message : 'An error occurred during initialization')
      
      // Trigger error haptic feedback
      const telegramService = TelegramService.getInstance()
      telegramService.triggerNotificationFeedback('error')
    } finally {
      setLoading(false)
    }
  }

  const handleTabChange = (tab: TabType) => {
    setActiveTab(tab)
    
    // Trigger haptic feedback on tab change
    const telegramService = TelegramService.getInstance()
    telegramService.triggerHapticFeedback('light')
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-blue-50 to-indigo-100 flex items-center justify-center">
        <div className="text-center p-8">
          <div className="animate-spin rounded-full h-16 w-16 border-b-4 border-blue-600 mx-auto mb-4"></div>
          <h2 className="text-xl font-semibold text-gray-800 mb-2">Loading TapWave</h2>
          <p className="text-gray-600">Initializing your experience...</p>
        </div>
      </div>
    )
  }

  if (error) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-red-50 to-pink-100 flex items-center justify-center">
        <div className="text-center p-8 bg-white rounded-2xl shadow-lg max-w-md mx-4">
          <div className="w-16 h-16 bg-red-100 rounded-full flex items-center justify-center mx-auto mb-4">
            <span className="text-red-600 text-2xl">‚ö†Ô∏è</span>
          </div>
          <h2 className="text-xl font-semibold text-red-600 mb-2">Error</h2>
          <p className="text-gray-600 mb-4">{error}</p>
          <button
            onClick={initializeApp}
            className="bg-red-600 text-white px-6 py-2 rounded-lg font-medium hover:bg-red-700 transition-colors"
          >
            Try Again
          </button>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-indigo-100">
      {/* Header */}
      <header className="bg-white shadow-sm border-b sticky top-0 z-10">
        <div className="px-4 py-4">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-2xl font-bold bg-gradient-to-r from-blue-600 to-indigo-600 bg-clip-text text-transparent">
                TapWave
              </h1>
              {user && (
                <p className="text-sm text-gray-600">
                  Welcome, {user.first_name}! üëã
                </p>
              )}
            </div>
            <div className="text-right">
              <div className="text-xs text-gray-500">Status</div>
              <div className="flex items-center">
                <div className="w-2 h-2 bg-green-500 rounded-full mr-2"></div>
                <span className="text-sm font-medium text-green-600">Online</span>
              </div>
            </div>
          </div>
        </div>
      </header>

      {/* Navigation */}
      <nav className="bg-white border-b sticky top-16 z-10">
        <div className="flex overflow-x-auto scrollbar-hide">
          {[
            { id: 'dashboard', label: 'Dashboard', emoji: 'üè†' },
            { id: 'tasks', label: 'Tasks', emoji: 'üìã' },
            { id: 'wallet', label: 'Wallet', emoji: 'üí∞' },
            { id: 'referrals', label: 'Referrals', emoji: 'üë•' },
            { id: 'withdraw', label: 'Withdraw', emoji: 'üí∏' },
          ].map((tab) => (
            <button
              key={tab.id}
              onClick={() => handleTabChange(tab.id as TabType)}
              className={`flex-shrink-0 px-4 py-3 text-sm font-medium whitespace-nowrap transition-all duration-200 ${
                activeTab === tab.id
                  ? 'text-blue-600 border-b-2 border-blue-600 bg-blue-50'
                  : 'text-gray-600 hover:text-gray-900 hover:bg-gray-50'
              }`}
            >
              <span className="mr-2">{tab.emoji}</span>
              {tab.label}
            </button>
          ))}
        </div>
      </nav>

      {/* Main Content */}
      <main className="p-4 pb-20">
        {activeTab === 'dashboard' && (
          <div className="space-y-6">
            <div className="bg-white rounded-2xl shadow-sm p-6">
              <h2 className="text-xl font-semibold mb-4 text-gray-800">
                Welcome to TapWave! üåä
              </h2>
              <p className="text-gray-600 mb-6">
                Watch ads, complete tasks, and earn rewards. Your earnings will be credited to your wallet instantly.
              </p>
              
              {/* Quick Stats */}
              <div className="grid grid-cols-2 gap-4 mb-6">
                <div className="bg-gradient-to-r from-blue-500 to-blue-600 rounded-xl p-4 text-white">
                  <div className="text-2xl font-bold">0</div>
                  <div className="text-sm opacity-90">Total Earned</div>
                </div>
                <div className="bg-gradient-to-r from-green-500 to-green-600 rounded-xl p-4 text-white">
                  <div className="text-2xl font-bold">0</div>
                  <div className="text-sm opacity-90">Tasks Completed</div>
                </div>
              </div>

              <AdComponent />
            </div>
          </div>
        )}

        {activeTab === 'tasks' && <TaskList />}
        {activeTab === 'wallet' && <Wallet />}
        {activeTab === 'referrals' && <Referrals />}
        {activeTab === 'withdraw' && <Withdraw />}
      </main>

      {/* Bottom Safe Area */}
      <div className="h-8 bg-transparent"></div>
    </div>
  )
}
